import fetch from '../config/fetch'
import {getStore} from '../config/mUtils'

export const accountLogin = (name, password) => fetch('/user/login_user.html', {name, password}, 'GET');
export const getUser = () => fetch('/user/queryUser_user.html', {uid: getStore('user_id')},'GET');


